@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;700&display=swap);
html, body{
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  background-color: #F5F5F5;
}

#root{
  height: 100%;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 12px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bg-orange{
  background-color: #FF8D00;
}

.bg-light-gray{
  background-color: #EEEEEE;
}

.text-dark-gray{
  color: #555555;
}

.text-white{
  color: #FFF;
}

.sorting-training-page{
    padding: 32px;
    display: flex;
    flex-direction: column;
    grid-gap: 36px;
    gap: 36px;
}

.sorting-training-page .header{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.sorting-training-page .header h1{
    font-weight: 700;
    font-size: 2.5rem;
    margin: 0;
}

@media only screen and (max-width: 900px) {
    .sorting-training-page {
        padding: 16px;
    }

    .sorting-training-page .header h1{
        font-size: 1.8rem;
    }
}

@media only screen and (max-width: 500px) {
    .sorting-training-page {
        padding: 8px;
    }

    .sorting-training-page .header h1{
        font-size: 1.5rem;
    }
}

@media only screen and (max-width: 400px) {
    .sorting-training-page .header {
        display: flex;
        flex-direction: column;
        justify-content: center;
        grid-gap: 16px;
        gap: 16px;
        padding-top: 16px;
        text-align: center;
    }
}

.Modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #FFFFFF;
    border-radius: 5px;
    width: 437px;
    height: 282px;
    max-height: 90vh;
    max-width: 90vw;

    display: flex;
    flex-direction: column;
}

.Overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.53);
}

.Modal .header{
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #CCCCCC;
}

.Modal .header .title{
    font-weight: 700;
    font-size: 18px;
    margin: 0;
}

.Modal .header .cross-icon{
    color: #999999;
    font-weight: 400;
    cursor: pointer;
}

.Modal .body{
    padding: 20px;
    flex-grow: 1;
    display: inline-flex;
    flex-direction: column;
    grid-gap: 14px;
    gap: 14px;
}

.Modal .body .description{
    margin: 0;
    font-weight: 400;
    font-size: 13px;
}

.Modal .body .input-box{
    border: 1px solid #CCCCCC;
    box-sizing: border-box;
    border-radius: 5px;
    height: 40px;
    font-size: 13px;
    padding: 13px;
}

.Modal .button-group{
    padding: 20px;
    border-top: 1px solid #CCCCCC;

    display: inline-flex;
    flex-wrap: wrap;
    grid-gap: 12px;
    gap: 12px;
    justify-content: flex-end;
}

.Modal .button-group .button{
    border-radius: 5px;
    width: 88px;
    height: 40px;
    border: none;
}

.Modal .button-group .button:hover:enabled{
    box-shadow: 1px 2px 10px -2px rgba(0,0,0,0.2);
    cursor: pointer;
}

.Modal .button-group .button:disabled{
    cursor: default;
    background-color: rgba(0,0,0, .3);
    color: rgba(0,0,0, .5);

}

.start-sorting-button{
    border-radius: 5px;
    width: 161px;
    height: 50px;
    border: none;
    background-color: #FF8D00;

    font-size: 15px;
    font-weight: bold;
    color: #FFFFFF;
}

.start-sorting-button:hover{
    box-shadow: 1px 2px 10px -2px rgba(0,0,0,0.2);
    cursor: pointer;
}

@media only screen and (max-width: 700px) {
    .start-sorting-button {
        width: 130px;
        height: 40px;
        font-size: 12px;
    }
}

@media only screen and (max-width: 500px) {
    .start-sorting-button {
        width: 100px;
    }
}

@media only screen and (max-width: 400px) {
    .start-sorting-button {
        width: 161px;
        height: 50px;
        font-size: 15px;
    }
}

.table{
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: #FFFFFF;
    border: 1px solid #DDDDDD;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.101972);
    border-radius: 5px;
}

.table .header{
    height: 72px;
    padding: 0 30px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-content: flex-end;

    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #000000;
}

.table .column-names, .row-template{
    display: flex;
    border: 1px solid #DDDDDD;
    border-bottom: none;
    height: 48px;

    color: #555555;
    font-weight: 400;
    font-size: 14px;
}

.table .column-name-cell, .row-cell{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 20px;
    flex-basis: 0;
}

.table .row-template{
    font-size: 14px;
    line-height: 18px;
    color: #000000;
}

.table .row-cell .tags{
    background: #EEEEEE;
    padding: 6px 10px;
    border-radius: 16px;
}

.table .column-names > .column-name-cell{
    border-right: 1px solid #DDDDDD;
}

.table .column-names > .column-name-cell ~ .column-name-cell{
    border-right: none;
}

.table .row-template > .row-cell{
    border-right: 1px solid #DDDDDD;
}

.table .row-template > .row-cell ~ .row-cell{
    border-right: none;
}

.table .email-data{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
}

#draggable-data-list .dragging-row{
    background-color: rgba(255, 141, 0, 0.5);
}

#draggable-data-list .data-row{
    height: 50px;
}

